<template>
	<div id="addResume">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">发布简历</div>
        <div class="van-nav-bar__right" @click="checkUpLoad">
          发布
        </div>
      </div>
    </div>
    <div class="login_box">
      <div style="background-color: white;position: relative">
        <div class="login_bar login_bottom" style="width: 70%;display: inline-block">
          <span class="title_span">
            <img src="static/img/icon_xm_tjps11.png" class="img_top_center"/>
          </span>
          <input class="font_14 login_input1" type="text" placeholder="姓名" v-model="uname"/>
        </div>
        <div class="login_bar" style="width: 70%;display: inline-block">
          <span class="title_span">性别</span>
          <select class="font_14 select-bar" v-model="sex">
            <option>男</option>
            <option>女</option>
          </select>
        </div>
        <img :src="headImg" class="head-img" @click="uploadImg()"/>
      </div>
      <div class="login_bar background_color_ffffff login_bottom border_top_eeeeee">
        <span class="title_span">学历</span>
        <input class="font_14 login_input" type="text" placeholder="请输入学历" v-model="xueli"/>
      </div>
      <div class="login_bar background_color_ffffff login_top login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">薪资要求</span>
        <select class="font_14 select-bar" v-model="price">
          <option>2000元左右</option>
          <option>3000-5000元</option>
          <option>5000-8000元</option>
          <option>10000元以上</option>
          <option>面议</option>
        </select>
        <span>月</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">工作年限</span>
        <select class="font_14 select-bar" v-model="age">
          <option>1年</option>
          <option>1-3年</option>
          <option>5年以上</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="title_span" style="display: inline-block;width: 20%;padding-right: 10px;">工作经历</span>
        <textarea class="font_14 login_input" style="width: 65%;vertical-align: text-top;" type="text" placeholder="壮士还没添加工作经历" v-model="content"/>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="title_span" style="display: inline-block;width: 20%;padding-right: 10px;">联系方式</span>
        <input class="font_14 login_input" type="text" placeholder="请填写联系方式" v-model="tell"/>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">求职意向</span>
        <input class="font_14 login_input" type="text" placeholder="请填写求职意向" v-model="title"/>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'addResume',
		data() {
			return {
        userId:localStorage.getItem('userId'),
        uname:'',
        sex:'男',
        age:'1年',
        tell:'',
        content:'',
        title:'',
        price:'2000元左右',
        type:6,
        xueli:'',
        headImg:'static/img/upLoadImg.png',
			}
		},
		methods:{
      uploadImg(){
        let $this = this;
        dsBridge.call('uploadHeadImg');
        dsBridge.register('addHeadImgCallback', function(rpdata) {
          $this.headImg = rpdata;
        })
      },
		  checkUpLoad(){
        if(!this.uname||!this.tell
          ||!this.xueli){
          this.$toast({
            message: '请将内容补充完整!',
            position: "bottom"
          });
        }else{
          this.addResume();
        }
      },
      addResume(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          uname: $this.uname,
          sex: $this.sex,
          age: $this.age,
          tell: $this.tell,
          content: $this.content,
          title: $this.title,
          price: $this.price,
          lable: $this.lable,
          metre: $this.metre,
          type: 6,
          xueli: $this.xueli,
          sameimg: $this.headImg,
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/addsamecity", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '发布成功!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .select-bar{
    width: 120px;
    background-color: #f4f4f4;
    padding: 5px;
    border-radius: 5px;
    color: #8a8a8a;
    margin-left: 8%;
    border: 0;
  }
  .head-img{
    padding: 10px;
    position: absolute;
    top: 10px;
    right: 0px;
    width: 16%;
  }
  .title_span{
    display: inline-block;
  }
  .img_service{
    width: 32.3%;
  }
  .login_bottom{
    border-bottom: 1px solid #eeeeee;
  }
  .login_span{
    position: relative;
    left: 46%;
    bottom: 32px;
    color: #ffffff;
  }
  .login_btn{
    width: 96%;
    margin-left: 2%;
  }
  .login_input1{
    margin-left: 0%;
    border: 0;
  }
  .login_input{
    margin-left: 8%;
    border: 0;
  }
  .login_input1{
    margin-left: 15%;
    border: 0;
  }
  .width_5{
    width: 5%;
  }
  .login_top{
    border-top: 5px solid #eeeeee;
  }
  .login_bar{
    padding: 15px 15px;
  }
  .login_box{
    margin-top: 50px;
  }
</style>



